.fullImg {
  height: 95vh;
  /* height: calc(100vh - 80px); header height */
}

.fullImg::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  background: url(/public/main.jpeg) no-repeat center center;
  background-size: cover;
  /* width: 100%; */
  height: 95vh;
  /* height: calc(100vh - 80px); header height */
  /* filter: saturate(0.3); */
  z-index: -1;
}

@media (min-width: 600px) {
  .fullImg::before {
    background-position: 0 70%;
  }
}
